<template>
  <div class="back">
    <div class="profile-container">
      <h3>个人资料</h3>
      <el-form :model="userInfo" label-width="100px" class="profile-form">
        <el-form-item label="用户名">
          <el-input v-model="userInfo.username" disabled />
        </el-form-item>
        <el-form-item label="密码">
          <el-input v-model="userInfo.password" />
        </el-form-item>
      </el-form>
      
    </div>
    <div class="button-container">
        <button class="change-password-button" @click="goToChangePassword">修改密码</button>
      </div>
  </div>
</template>

<script setup>
import { onMounted, ref } from 'vue';
import { useRouter } from 'vue-router';

const router = useRouter();

const userInfo = ref({
  username: '',
  password: ''
});

const goToChangePassword = () => {
  router.push('/pass');
};

onMounted(() => {
  userInfo.value.username = localStorage.getItem('username') || '用户';
  userInfo.value.password = localStorage.getItem('password') || '密码'; 
});
</script>

<style scoped>
.change-password-button {
  width: 25%;
  background-color: #ff4081;
