   margin: 20px auto; 
  padding: 20px;
  border-radius: 12px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
  background: linear-gradient(135deg, #fbc2eb, #a6c0fe);
  position: relative;
  overflow: hidden;
  max-width: 800px; 
  max-height: 100%;
}

.profile-container::before {
  content: '';
  position: absolute;
  top: -30%;
