}

.profile-form .el-form-item {
  margin-bottom: 20px;
}

.profile-form .el-form-item label {
  color: #ff4081;
  font-weight: bold;
  font-size: 16px;
}

.profile-form .el-input {
  border-radius: 8px;
  border: 2px solid #ff4081;
  background-color: #f9f9f9;
  transition: border-color 0.3s ease;
}

.profile-form .el-input:focus {
  border-color: #ff80ab;
